import { Button, Typography, Paper, Stack } from "@mui/material";
import { useEffect, useState } from "react";
import { useParams, Link, Prompt } from "react-router-dom";
import { v4 as uuid } from "uuid";
import SummaryView from "./SummaryView";
import SummaryItemData from "./SummaryItemData";
import { format, subDays } from "date-fns";
import { SecondsToHoursMinutesAndSeconds } from "../utils/helpers/HelperFunctions";

function Timer({ timers, setTimers, user }) {
  /**************************************************************************/

  const { id } = useParams();

  const timer = timers.find((timer) => timer.id === id);

  const [elapsedTime, setElapsedTime] = useState(
    !timer.counter ? 0 : timer.counter
  );

  const [timerUsedTime, setTimerUsedTime] = useState({});
  const [intervalId, setIntervalId] = useState(0);
  const [buttonDisabled, setButtonDisabled] = useState();
  const [ifNavigate, setIfNavigate] = useState(false);

  /**************************************************************************/

  // Ajastimen kynnistys
  const startTimer = () => {
    setButtonDisabled(true);
    setIfNavigate(true);
    // Jos ensimminen kerta, tallennetaan ensimmisen kynnistyksen aika ja listn ajastimien array
    if (!timer.firstStart) {
      const newTimer = {
        ...timer,
        firstStart: new Date().getTime(),
        timerUsed: [],
      };
      setTimers({
        ...user,
        timers: timers.map((originalTimer) =>
          originalTimer.id !== id ? originalTimer : newTimer
        ),
      });
    }
    setTimerUsedTime({
      id: uuid(),
      startTime: elapsedTime,
    });
    // Kynnistetn ajastimen intervalli
    let newIntervalId = setInterval(() => {
      setElapsedTime((prevCount) => prevCount + 1);
    }, 1000);
    setIntervalId(newIntervalId);
  };

  /**************************************************************************/

  // Ajastimen pysytys
  const stopTimer = () => {
    if (intervalId) {
      setButtonDisabled(false);
      setIfNavigate(false);
      clearInterval(intervalId);
      setIntervalId(0);
      setTimerUsedTime({
        ...timerUsedTime,
        stopTime: elapsedTime,
        currentEndTime: new Date().getTime(),
      });
      return;
    }
  };

  /**************************************************************************/

  // Tallennetaan tiedot kyttjlle
  useEffect(() => {
    // Poistutaan jos ei ole lopetusaikaa tai aika ei kerke kulua vhintn sekuntia.
    if (
      !timerUsedTime.stopTime ||
      timerUsedTime.startTime === timerUsedTime.stopTime
    )
      return;

    const newTimer = {
      ...timer,
      counter: elapsedTime,
      timerUsed: [...timer.timerUsed, timerUsedTime],
    };
    setTimers({
      ...user,
      timers: timers.map((originalTimer) =>
        originalTimer.id !== id ? originalTimer : newTimer
      ),
    });
  }, [buttonDisabled]);

  /**************************************************************************/

  // Ajan nyttminen
  const displayedTime = (time) => {
    const seconds = `0${time % 60}`.slice(-2);
    const minutes = `0${Math.floor((time / 60) % 60)}`.slice(-2);
    const hours =
      time < 360000
        ? `0${Math.floor(time / 3600)}`.slice(-2)
        : `${Math.floor(time / 3600)}`;

    if (hours === "00") {
      return `${minutes}:${seconds}`;
    }

    return `${hours}:${minutes}:${seconds}`;
  };

  /**************************************************************************/

  // Ajastimen kytt viimeisten pivien/tuntien aikana
  const usedLastDays = (days) => {
    if (!timer.timerUsed) return;

    const startDay = subDays(new Date(), days);

    const lastDays = timer.timerUsed
      .filter((timer) => timer.currentEndTime >= startDay)
      .map((timer) => timer.stopTime - timer.startTime)
      .flatMap((timer) => timer)
      .reduce((prev, cur) => prev + cur, 0);
    return lastDays;
  };

  /**************************************************************************/

  // Pisin yksittinen ajastimen kynnissolo
  const maxTimerRunning = () => {
    if (!timer.timerUsed || timer.timerUsed.length === 0) return;

    // Palautetaan timer objecti joka on ollut pisimpn yhdenjaksoisesti pll.
    const timerObj = timer.timerUsed.reduce(
      (prev, cur) =>
        prev.stopTime - prev.startTime > cur.stopTime - cur.startTime
          ? prev
          : cur,
      0
    );
    return `${format(
      timerObj.currentEndTime,
      "d.M"
    )} pll ${SecondsToHoursMinutesAndSeconds(
      timerObj.stopTime - timerObj.startTime
    )}`;
  };

  /**************************************************************************/
  return (
    <Stack spacing={2}>
      <Prompt
        when={ifNavigate}
        message="Haluatko varmasti poistua? Ajastimen arvoa ei tallenneta jos poistut sen ollessa kynniss!"
      />
      <Typography variant="h5" component="h1" textAlign="center">
        {timer.timerName}
      </Typography>
      <Paper elevation={4} sx={{ width: "100%", p: 2 }}>
        <Typography sx={{ textAlign: "center" }}>Kytetty aika</Typography>
        <Typography variant="h2" component="p" sx={{ textAlign: "center" }}>
          {displayedTime(elapsedTime)}
        </Typography>
      </Paper>
      <Stack direction="row" spacing={1} justifyContent="center">
        <Button
          onClick={startTimer}
          disabled={buttonDisabled}
          variant="contained"
          color="success"
          size="small"
        >
          Kynnist ajastin
        </Button>
        <Button
          onClick={stopTimer}
          disabled={!buttonDisabled}
          variant="contained"
          color="error"
          size="small"
        >
          Pysyt ajastin
        </Button>
      </Stack>
      <SummaryView
        title="Tmn ajastimen yhteenveto"
        placeOne={
          <SummaryItemData
            title="Ensimminen kynnistys"
            sum={
              timer.firstStart
                ? format(timer.firstStart, "d.M.y HH:mm")
                : "Ei viel kynnistetty"
            }
          />
        }
        placeTwo={
          <SummaryItemData
            title="Pisin aika yhdell kertaa"
            sum={
              !maxTimerRunning() ? "Ei viel kynnistetty" : maxTimerRunning()
            }
          />
        }
        placeThree={
          <SummaryItemData
            title="Ajankytt viimeinen vrk."
            sum={SecondsToHoursMinutesAndSeconds(usedLastDays(1))}
          />
        }
        placeFour={
          <SummaryItemData
            title="Ajankytt viim. 7 vrk."
            sum={SecondsToHoursMinutesAndSeconds(usedLastDays(7))}
          />
        }
      />
      <Button
        variant="contained"
        color="error"
        size="small"
        disabled={buttonDisabled}
        component={Link}
        to={`/delete/${timer.id}`}
      >
        Poista laskuri
      </Button>
    </Stack>
  );
}

export default Timer;
