import { subDays } from "date-fns";
import SummaryItemData from "./SummaryItemData";
import SummaryView from "./SummaryView";
import { SecondsToHoursMinutesAndSeconds } from "../utils/helpers/HelperFunctions";

function Summary({ timers }) {
  /**************************************************************************/
  // Yhteens kaikkiin tehtviin kytetty aika.
  const timersSum = timers
    .map((timer) => (timer.counter ? timer.counter : 0))
    .reduce((p, c) => p + c, 0);

  /**************************************************************************/

  // Tehtv johon kytetty eniten aikaa.
  const usedTheMostTime = timers.reduce((prev, current) => {
    let prevValue = prev.counter ?? 0; // Jos ei viel arvoa, asetetaan nolla
    let currValue = current.counter ?? 0;
    return prevValue > currValue ? prev : current; // Palauttaa objectin jonka counter arvo on suurin
  }, 0);

  /**************************************************************************/

  // Nyt kytety aika viimeisten vuorokausien osalta, ajan voi mritt itse antamalla parametrin
  const getTimersSum = (days) => {
    const startDay = subDays(new Date(), days);

    // Kydn tarvittavat arrayt lpi, yhdsitetn ne yhdeksi arrayksi ja lasketaan laskurien arvot yhteen.
    const getSum = timers
      .map((timer) => timer.timerUsed)
      .filter((timer) => timer !== undefined) // Ei oteta kynnistmttmi huomioon
      .flatMap((timer) => timer)
      .filter((timer) => timer.currentEndTime >= startDay) // Vain ajastimet jotka on halutun vrk ajan jlkeen
      .map((timer) => timer.stopTime - timer.startTime)
      .reduce((prev, cur) => prev + cur, 0);

    return getSum;
  };

  /**************************************************************************/

  return (
    <>
      {timers.length !== 0 ? (
        <SummaryView
          title="Yhteenveto"
          placeOne={
            <SummaryItemData
              title="Ajankytt yhteens"
              sum={SecondsToHoursMinutesAndSeconds(timersSum)}
            />
          }
          placeTwo={
            <SummaryItemData
              title="Kytetty eniten aikaa"
              sum={usedTheMostTime.timerName}
            />
          }
          placeThree={
            <SummaryItemData
              title="Viimeiset 7 vrk."
              sum={SecondsToHoursMinutesAndSeconds(getTimersSum(7))}
            />
          }
          placeFour={
            <SummaryItemData
              title="Viimeiset 30 vrk."
              sum={SecondsToHoursMinutesAndSeconds(getTimersSum(30))}
            />
          }
        />
      ) : (
        ""
      )}
    </>
  );
}
export default Summary;
