import { Typography, Stack, Button } from "@mui/material";
import { Link } from "react-router-dom";
import { CapitalizeWord } from "../utils/helpers/HelperFunctions";

function InfoBox({ user }) {
  const time = new Date();
  /**************************************************************************/

  // Tervehdys
  const greetingText = (time) => {
    if (time > 6 && time < 10) {
      return "Hyv huomenta,";
    } else if (time >= 10 && time < 15) {
      return "Hyv piv,";
    } else if (time >= 15 && time < 18) {
      return "Hyv iltapiv,";
    } else if (time >= 18 && time < 22) {
      return "Hyv iltaa,";
    } else {
      return "Hyv yt,";
    }
  };

  /**************************************************************************/

  return (
    <Stack spacing={1}>
      <Typography variant="h5" component="h1">{`${greetingText(
        time.getHours()
      )} ${user.name ? CapitalizeWord(user.name) : ""}`}</Typography>
      {user.timers.length === 0 ? (
        <Stack spacing={2}>
          <Typography>
            Sinulla ei ole viel yhtn ajastinta, haluaisitko list yhden?
          </Typography>
          <Button variant="contained" fullWidth component={Link} to="/addnew">
            Lis ajastin
          </Button>
        </Stack>
      ) : (
        ""
      )}
    </Stack>
  );
}
export default InfoBox;
